import Vue from 'vue'
import Router from 'vue-router'
import login from './views/login.vue'
import index from './views/index.vue'
import store from './store'
import register from './views/register'
Vue.use(Router)
//二级路由
import mainindex from '@/components/secondrouter/mainindex'
import company from '@/components/secondrouter/company'
import contactus from '@/components/secondrouter/contactus'
import mainintro from '@/components/secondrouter/mainintro'
import outstandingstudent from '@/components/secondrouter/outstandingstudent'
import teachpower from '@/components/secondrouter/teachpower'

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [{
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/register',
      name: 'register',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component:register
    },
    {
      path: '/index',
      name: 'index',
      component: index,
      redirect: '/index/mainshow/0',
    
      children: [{

        path: '/index/mainshow/:id',
        name: 'mainindex0',
        component: mainindex

      }, {

        path: '/index/mainshow/:id',
        name: 'mainindex1',
        component: company

      }, {

        path: '/index/mainshow/:id',
        name: 'mainindex2',
        component: contactus

      }, {

        path: '/index/mainshow/:id',
        name: 'mainindex3',
        component: mainintro

      }, {

        path: '/index/mainshow/:id',
        name: 'mainindex4',
        component: outstandingstudent

      },{

        path: '/index/mainshow/:id',
        name: 'mainindex5',
        component: teachpower

      }]
    },
  ]
})