<template>
  <div class="login">
    <p style="color:#fff">讯科欢迎你</p>

 <input type="text" name="" id="" placeholder="输入ID或者用户名登录"  class="br" v-model="username">
   <p><input type="text" name="" id="" placeholder="输入密码"  class="br" v-model="password"></p>
   <el-button type="info" round :class="{bgcolor:isshow}" style="width:100%" @click="toindex">登录</el-button>
   <p @click="toRegister" style="color:#fff;cursor: pointer;">还没注册账号</p>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  name: "home",
  data() {
    return {
      input: "",
      username: "",
      password: "",
      isshow:false
    };
  },
   updated(){
     if(this.password!=''&&this.username!=''){
       this.isshow=true
     }else{
       this.isshow=false
     }
  },
  methods: {
    toRegister() {
      this.$router.push({ name: "register" });
    },
    ...mapMutations(["login"]),
    toindex() {
      let username = this.username;
      let password = this.password;
      let that = this;
      this.login({
        username,
        password,
        function() {
          setTimeout(function() {
            that.$router.push({ name: "index" });
          }, 1500);
        }
      });
    }
  }
};
</script>
