<template>
  <div class="register"> 
    <div class="contain">
        <p class="re">注册</p>
        
        <div class="demo-input-suffix">
        <p class="line">  <span> 用户名：</span>
          
              <input type="text" name="" id="" placeholder="用户名/手机"  class="br" v-model="username">
              </p>
      </div>
      <div class="demo-input-suffix">
        <p class="line"> <span> 密码：</span>
          <input type="text" name="" id="" placeholder="密码"  class="br" v-model="password1">
      </p>
      </div>
      <div class="demo-input-suffix">
        <p class="line"> <span>确认密码：</span>
        <input type="text" name="" id="" placeholder="确认密码"  class="br" v-model="password2">
      </p>
      </div>
      <div class="demo-input-suffix">
        <p class="line">  <el-button type="info"   @click="back">返回</el-button>
        <el-button type="info" :class="{bgcolor:isshow}" style="width:100%" @click="reg">注册</el-button>
      </p>
      </div>
  </div>
  </div>
</template>
<script>
import {mapMutations} from'vuex'
import {
  loginsuccess
} from '../components/until.js'
export default {
  data(){
    return{
      username:'',
      password1:'',
      password2:'',
      isshow:false
    }
  },

  updated(){
     if(this.password1!=''&&this.password2!=''&&this.username!=''){
       this.isshow=true
     }else{
       this.isshow=false
     }
  },
  methods:{
    ...mapMutations(['register']),
    reg(){
     let username=this.username
     let password=this.password2

      if(this.password1!=this.password2){
        loginsuccess('密码不一致')
        return
      }else if(this.password1==''||this.username==''||this.password2==''){
         loginsuccess('账号密码不能为空')
         return
      }else{
        
        let that=this
        this.register({username,password,function () {
          // console.log(that)
          
          setTimeout(function(){  that.$router.push({name:'login'})},1500)
          
         
        }})
      }
    },
  back(){
    this.$router.push('/')
  }
  }
}
</script>

