import Vue from 'vue'
import Vuex from 'vuex'
import Axios from '../node_modules/axios';
Vue.use(Vuex)
const HOST = 'https://api.uec.app/api/v1'
Axios.defaults.baseURL = HOST
import {
  loginsuccess
} from "./components/until";
export default new Vuex.Store({
  state: {
    token: '',
    username: '',
    isLogin:false,
    currentlocation:[],
    indexasidedata: [{
      icon: 'fa fa-tachometer',
      project: '综合首页'
    }, {
      icon: 'fa fa-desktop',
      project: '课程介绍 '
    }, {
      icon: 'fa fa-balance-scale',
      project: '师资力量'
    }, {
      icon: 'fa fa-handshake-o',
      project: '合作企业'
    }, {
      icon: 'fa fa-users',
      project: '优秀学员'
    }, {
      icon: 'fa fa-volume-control-phone',
      project: '联系我们'
    }]
  },
  mutations: {
    quitlogin(state,um){
      state.token=''
      state.isLogin=false
      loginsuccess("退出登录", um.function())
    },
    login(state, data) {
      let username = data.username
      let password = data.password
      Axios.get("https://api.uec.app/api/v1/login", {

        params: {
          username: username,
          password: password
        }
      }).then((res) => {
         
        if (res.data.code == 100) {
         
         
          loginsuccess(res.data.msg)
         
          
        } else if (res.data.code == 0) {  
          state.isLogin=true
          console.log(state.isLogin)
          state.username = res.data.data.username
          state.token = res.data.data._token
          loginsuccess("登录成功", data.function())
        }
      })
    },
    register(state, data) {
      let username = data.username
      let password = data.password


      Axios.get('/register', {
        params: {
          username: username,
          password: password
        }
      }).then((res) => {

        if (res.data.code == 200) {
          loginsuccess(res.data.msg, )


        } else if (res.data.code == 0) {
          loginsuccess("注册成功", data.function())

          //  this.$router.push()
        }

      })
    },
    recodelocation(state,um){
      state.currentlocation.push(um)
    }
  },
  actions: {

  }
})